@use "sass:map";
@use "sass:list";
$map: (
  min-width: 200px,
  max-width: 1000px
);
$map2:(
color: red
);
$sizes: map.get($map, max-width), map.get($map2, color);
        header .menu{
            li{
              &:first-child{
                background-color: aqua;
                max-width: nth($sizes, 1);
                min-width: nth($sizes, 2);
              }
              &:last-child{
                background-color: nth($sizes,2);
                min-width: nth($sizes,1);
              }
            }
        }













